.what-if {
	position: relative;
	margin-bottom: 255px;
	z-index: 1;
	&__title {
		@extend %section-title;
		margin-bottom: 30px;
	}
	&__text {
		margin-bottom: 50px;
		max-width: 660px;
		p {
			@extend %p_2;
			margin-top: 0;
			margin-bottom: 20px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	&__registration {
		margin-left: 170px;
		background-color: #fff;
	}
	&__img {
		position: absolute;
		bottom: -190px;
		right: -75px;
	}
	&__decoration {
		display: none;
	}

	@include _mobile {
		margin-bottom: 195px;
		&__title {
			margin-bottom: 20px;
		}
		&__text {
			margin-bottom: 107px;
		}
		&__img {
			width: 308px;
			height: 350px;
			right: -120px;
			z-index: -1;
			bottom: -70px;
		}
		&__decoration {
			height: 800px;
			display: block;
			position: absolute;
			right: -20px;
			width: 600px;
			top: -100px;
			z-index: -2;
		}
	}
	@include _tablet {
		margin-bottom: 100px;
		&__img {
			width: 250px;
			bottom: -50px;
			z-index: -1;
			right: 0;
		}
	}
}

.what-if-registration {
	padding: 30px 20px;
	border: 2px solid #b8dbee;
	box-shadow: 0px 4px 10px rgba(15, 5, 76, 0.15);
	backdrop-filter: blur(14px);
	border-radius: 40px;
	max-width: 520px;
	display: flex;
	align-items: center;
	justify-content: center;
	align-content: center;
	flex-direction: column;
	position: relative;
	text-align: center;
	&::after {
		content: '';
		width: 38px;
		height: 78px;
		background-image: url(../img/triangle-right.svg);
		background-repeat: no-repeat;
		background-position: center center;
		background-size: 100% 100%;
		position: absolute;
		left: 100%;
		top: 40px;
		display: block;
	}
	&__title {
		@extend %title_1;
		margin-bottom: 15px;
	}
	p {
		@extend %p_3;
		max-width: 390px;
		margin-bottom: 50px;
		span {
			font-weight: bold;
			background: linear-gradient(
				0deg,
				rgba(221, 26, 143, 1) 0%,
				rgba(254, 17, 126, 1) 100%
			);
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
		}
	}
	@include _mobile {
		margin-left: 0;
		padding: 12px 10px;
		width: 84%;
		&::after {
			width: 18px;
			height: 32px;
		}
		&__title {
			margin-bottom: 10px;
		}
		p {
			margin-bottom: 10px;
		}
		.btn__primary {
			font-size: 10px;
			line-height: 13px;
		}
	}
	@include _tablet {
		margin-left: 0;
	}
}
