.stages-card {
	max-width: 210px;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	align-content: flex-start;
	flex-direction: column;
	&__number {
		@extend %title_3;
		margin-bottom: 15px;
		&_blue {
			color: $accent-color;
		}
		&_pink {
			background: linear-gradient(
				0deg,
				rgba(221, 26, 143, 1) 0%,
				rgba(254, 17, 126, 1) 100%
			);
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
		}
	}
	&__icon {
		width: 30px;
		height: 30px;
		margin-bottom: 20px;
		border-radius: 50%;
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		align-content: center;
		transition: 0.3s all;
		&::before {
			content: '';
			position: absolute;
			width: 40px;
			height: 40px;
			filter: blur(2px);
			opacity: 0;
			visibility: hidden;
			transition: 0.6s all;
			border-radius: 50%;
		}
		&::after {
			content: '';
			width: 28px;
			height: 28px;
			border-radius: 50%;
			background-color: transparent;
			border: 4px solid #fff;
			position: absolute;
		}
		&_gradient {
			background: linear-gradient(
				180deg,
				#180d5b 0%,
				rgba(36, 18, 106, 0.94) 23.24%,
				#1f72ba 56.05%,
				#42e8e0 85.74%
			);
			&::before {
				background: linear-gradient(
					180deg,
					#180d5b 0%,
					rgba(36, 18, 106, 0.94) 23.24%,
					#1f72ba 56.05%,
					#42e8e0 85.74%
				);
			}
		}
		&_blue {
			background: linear-gradient(180deg, #42e8e0 0%, #40b6cb 100%);
			&::before {
				background: linear-gradient(180deg, #42e8e0 0%, #40b6cb 100%);
			}
		}
		&_pink {
			background: linear-gradient(180deg, #ff6dba 0%, #fe117e 100%);
			&::before {
				background: linear-gradient(180deg, #ff6dba 0%, #fe117e 100%);
			}
		}
	}

	&__text {
		@extend %p_12;
		color: #6d6d6d;
		margin-top: 0;
		margin-bottom: 0;
		position: relative;
		padding-bottom: 10px;
		margin-bottom: 10px;
		&:last-child {
			padding-bottom: 0;
			margin-bottom: 0;
			&::after {
				content: none;
			}
		}
		&::after {
			content: '';
			width: 100px;
			height: 1px;
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%);
			background-color: $accent-color;
		}
		.blue {
			color: $accent-color;
			font-weight: bold;
		}
		.pink {
			color: #ff6dba;
			font-weight: bold;
		}
	}
	&:hover & {
		&__icon::before {
			opacity: 1;
			visibility: visible;
		}
	}
	@include _mobile {
		&__number {
			margin-bottom: 10px;
		}
		&__icon {
			width: 20px;
			height: 20px;
			&::before {
				width: 25px;
				height: 25px;
			}
			&::after {
				width: 18px;
				height: 18px;
				border-width: 2px;
			}
		}
		&__text {
			&::after {
				width: 80px;
			}
		}
	}
}
