html {
	box-sizing: border-box;
}
*,
*::before,
*::after {
	box-sizing: inherit;
}
body {
	font-family: $primary-font, -apple-system, BlinkMacSystemFont, 'Segoe UI',
		Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
	overflow-x: hidden;
}
// .clearfix::after {
// 	content: '';
// 	display: table;
// 	clear: both;
// }
img {
	display: block;
	max-width: 100%;
	height: auto;
	margin: 0;
	padding: 0;
}
button,
input {
	outline: none;
	border: none;
	background-color: transparent;
	cursor: pointer;
}

%section-title {
	font-weight: normal;
	font-size: 40px;
	line-height: 60px;
	letter-spacing: 0.05em;
	text-transform: uppercase;
	color: $text-color;
	span {
		color: $accent-color;
		font-weight: 900;
	}
	@include _mobile {
		font-size: 26px;
		line-height: 30px;
	}
}

%title {
	&_1 {
		font-weight: bold;
		font-size: 20px;
		line-height: 28px;
		text-align: center;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: $accent-color;
	}
	&_2 {
		font-weight: bold;
		font-size: 20px;
		line-height: 28px;
		text-align: center;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: $text-color;
		span {
			font-weight: bold;
			font-size: 24px;
			line-height: 28px;
		}
	}
	&_3 {
		font-weight: bold;
		font-size: 18px;
		line-height: 18px;
		text-align: center;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: $text-color;
	}
	@include _mobile {
		&_1 {
			font-size: 12px;
			line-height: 14px;
		}
		&_2 {
			font-size: 16px;
			line-height: 24px;
			span {
				font-size: 20px;
				line-height: 24px;
			}
		}
		&_3 {
			font-size: 16px;
		}
	}
}
%subtitle {
	&_1 {
		font-weight: normal;
		font-size: 18px;
		line-height: 24px;
		text-align: center;
		letter-spacing: 0.05em;
		color: $text-color;
	}
	&_2 {
		font-weight: bold;
		font-size: 24px;
		line-height: 34px;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: #292268;
	}
	&_3 {
		font-weight: 900;
		font-size: 20px;
		line-height: 30px;
		letter-spacing: 0.05em;
		text-transform: uppercase;
	}
	@include _mobile {
		&_1 {
			font-size: 16px;
			line-height: 20px;
		}
		&_2 {
			font-size: 18px;
			line-height: 28px;
		}
		&_3 {
			font-size: 18px;
			line-height: 30px;
		}
	}
}
%p {
	&_1 {
		font-weight: normal;
		font-size: 20px;
		line-height: 28px;
		letter-spacing: 0.05em;
		color: #000000;
		margin-top: 0;
	}
	&_2 {
		font-weight: normal;
		font-size: 18px;
		line-height: 20px;
		letter-spacing: 0.05em;
		color: #000000;
		margin-top: 0;
	}
	&_3 {
		font-weight: normal;
		font-size: 16px;
		line-height: 20px;
		letter-spacing: 0.05em;
		color: #6d6d6d;
		margin-top: 0;
	}
	&_4 {
		font-weight: bold;
		font-size: 16px;
		line-height: 24px;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: $text-color;
	}
	&_5 {
		font-weight: bold;
		font-size: 20px;
		line-height: 30px;
		letter-spacing: 0.05em;
		color: $text-color;
	}
	&_6 {
		font-weight: bold;
		font-size: 16px;
		line-height: 28px;
		letter-spacing: 0.05em;
		text-transform: uppercase;
	}
	&_7 {
		font-weight: 600;
		font-size: 16px;
		line-height: 22px;
		letter-spacing: 0.05em;
		color: #000000;
	}
	&_8 {
		font-weight: normal;
		font-size: 13px;
		line-height: 20px;
		letter-spacing: 0.05em;
	}
	&_9 {
		font-weight: 600;
		font-size: 16px;
		line-height: 24px;
		letter-spacing: 0.05em;
		color: $text-color;
	}
	&_10 {
		font-weight: bold;
		font-size: 22px;
		line-height: 20px;
		letter-spacing: 0.05em;
	}
	&_11 {
		font-weight: normal;
		font-size: 14px;
		line-height: 20px;
		text-align: center;
		letter-spacing: 0.05em;
	}
	&_12 {
		font-weight: normal;
		font-size: 14px;
		line-height: 18px;
		text-align: center;
		letter-spacing: 0.05em;
	}
	&_13 {
		font-weight: bold;
		font-size: 24px;
		line-height: 34px;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: #292268;
	}
	&_14 {
		font-weight: normal;
		font-size: 16px;
		line-height: 22px;
		text-align: center;
		letter-spacing: 0.05em;
		color: #000;
	}
	@include _mobile {
		&_1 {
			font-size: 16px;
			line-height: 22px;
		}
		&_2 {
			font-size: 14px;
			line-height: 16px;
		}
		&_3 {
			font-size: 11.2298px;
			line-height: 14px;
		}
		&_4 {
			font-size: 12px;
			line-height: 18px;
		}
		&_5 {
			font-size: 16px;
			line-height: 22px;
		}
		&_6 {
			font-size: 10px;
			line-height: 12px;
		}
		&_7 {
			font-size: 14px;
			line-height: 20px;
		}
		&_8 {
			font-size: 12px;
			line-height: 18px;
		}
		&_9 {
			font-size: 14px;
			line-height: 26px;
		}
		&_11 {
			font-size: 12px;
			line-height: 20px;
		}
		&_12 {
			font-size: 12px;
			line-height: 16px;
		}
		&_13 {
			font-size: 20px;
			line-height: 34px;
		}
		&_14 {
			font-size: 14px;
			line-height: 16px;
		}
	}
	@include _tablet {
		&_1 {
			font-size: 18px;
			line-height: 20px;
		}
	}
}
