.choice {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	align-content: flex-start;
	flex-direction: column;
	width: 100%;
	margin-bottom: 55px;
	&__title {
		@extend %section-title;
		span {
			display: block;
		}
		margin-bottom: 75px;
		margin-top: 0;
	}
	&__row {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		align-content: space-between;
		margin-bottom: 70px;
		flex-direction: row;
		&_revers {
			flex-direction: row-reverse;
			.choice__text {
				text-align: right;
			}
			.btn__primary {
				margin-left: auto;
			}
			.choice__subtitle::after {
				left: auto;
				right: 0;
			}
		}
	}
	&__text {
		max-width: 650px;
		width: 100%;
		p {
			@extend %p_1;
			margin-bottom: 35px;
		}
	}
	&__subtitle {
		font-weight: bold;
		font-size: 30px;
		line-height: 1;
		position: relative;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: $text-color;
		padding-bottom: 25px;
		display: block;
		margin-bottom: 40px;
		&::after {
			content: '';
			width: 150px;
			height: 2px;
			position: absolute;
			bottom: 0;
			left: 0;
			background: linear-gradient(
				0deg,
				rgba(66, 232, 224, 1) 0%,
				rgba(64, 182, 203, 1) 100%
			);
		}
	}
	&__img {
	}
	.btn__primary {
		padding: 17px 20px;
		margin-bottom: 35px;
	}
	.btn__blue-border {
		color: $text-color;
	}
	&__request {
		display: flex;
		align-items: center;
		justify-content: space-between;
		align-content: space-between;
		width: 100%;
		margin-bottom: 100px;
		.btn__primary {
			margin-bottom: 0;
		}
		p {
			font-weight: normal;
			max-width: 780px;
			font-size: 17px;
			line-height: 20px;
			letter-spacing: 0.05em;
			color: $text-color;
			margin-top: 0;
			margin-bottom: 0;
		}
	}
	&__notice {
		p {
			@extend %p_3;
			span {
				color: $accent-color;
				font-weight: bold;
			}
		}
	}
	@include _mobile {
		&__title {
			text-align: center;
			margin-bottom: 60px;
		}
		&__subtitle {
			font-size: 24px;
			line-height: 28px;
			text-align: center;
			&::after {
				width: 130px;
				left: 50%;
				transform: translateX(-50%);
			}
		}
		&__row {
			flex-direction: column;
			&_revers {
				.choice__text {
					text-align: center;
				}
				.btn__primary {
					margin-right: auto;
				}
				.choice__img {
					margin-bottom: 30px;
				}
			}
		}
		&__text {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			text-align: center;
			p {
				margin-bottom: 40px;
			}
		}
		&__img {
			height: 323px;
			img {
				height: 100%;
			}
		}
		&__request {
			text-align: center;
			flex-direction: column;
			justify-content: center;
			margin-bottom: 70px;
			p {
				margin-bottom: 30px;
			}
		}
		.btn__primary {
			padding: 13px 20px;
		}
		&__notice {
			text-align: right;
		}
	}
	@include _tablet {
		&__request {
			.btn__primary {
				margin-left: 20px;
			}
		}
	}
}
