.advantages {
	margin-bottom: 130px;
	&__title {
		@extend %section-title;
		text-align: center;
		margin-bottom: 70px;
		margin-top: 0;
	}
	&__row {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		align-content: flex-start;
		flex-direction: row;
		flex-wrap: wrap;
	}
	&__card {
		margin-right: 30px;
		&:nth-child(4n + 2) {
			padding-top: 80px;
		}
		&:nth-child(4n + 3) {
			padding-top: 160px;
		}
		&:nth-child(4n + 4) {
			padding-top: 240px;
			margin-right: 0;
		}
		&:last-child {
			margin-right: 0;
		}
	}

	@include _mobile {
		&__row {
			flex-direction: column;
			justify-content: center;
			align-content: center;
		}
		&__card {
			margin-right: 0;
			padding: 0;
			margin-bottom: 60px;
			&:last-child {
				margin-bottom: 0;
			}
			&:nth-child(4n + 2),
			&:nth-child(4n + 3),
			&:nth-child(4n + 4) {
				padding: 0;
			}
		}
	}
	@include _tablet {
		&__row {
			justify-content: center;
		}
		&__card {
			&:nth-child(even) {
				margin-right: 0;
			}
		}
	}
}

.advantages-card {
	max-width: 260px;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	align-content: flex-start;
	flex-direction: column;
	text-align: center;
	&__img {
		max-width: 170px;
		height: 230px;
		margin-bottom: 30px;
	}
	&__title {
		font-weight: 600;
		font-size: 18px;
		line-height: 24px;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: $text-color;
		margin-top: 0;
		margin-bottom: 25px;
	}
	&__list {
		list-style: none;
		padding: 0;
		margin: 0;
	}
	&__list-item {
		font-weight: normal;
		font-size: 14px;
		line-height: 18px;
		letter-spacing: 0.05em;
		color: #6d6d6d;
		padding-bottom: 15px;
		margin-bottom: 15px;
		position: relative;
		span {
			color: $accent-color;
			font-weight: 900;
		}
		&:last-child::after {
			content: none;
		}
		&::after {
			content: '';
			height: 1px;
			width: 130px;
			background-color: $accent-color;
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%);
		}
	}
	@include _mobile {
		&__img {
			max-width: 130px;
			height: 200px;
			margin-bottom: 40px;
			img {
				width: auto;
				height: 100%;
			}
		}
		&__title {
			font-size: 16px;
			line-height: 24px;
		}
	}
}
