.hero {
	position: relative;
	z-index: 1;
	min-height: 795px;
	display: flex;
	align-items: center;
	justify-content: center;
	align-content: center;
	padding-top: 105px;
	top: -105px;
	&__container {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		align-content: flex-start;
		height: 100%;
		position: relative;
	}
	&__text {
		max-width: 555px;
		width: 100%;
		position: relative;
		z-index: 2;
	}
	&__title {
		margin-top: 0;
		margin-bottom: 0;
		font-weight: 600;
		font-size: 50px;
		line-height: 60px;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: $text-color;
		margin-bottom: 25px;
		span {
			color: $accent-color;
			font-weight: 900;
			&:first-child {
				display: block;
				font-size: 60px;
			}
		}
	}
	&__subtitle {
		font-weight: normal;
		font-size: 18px;
		line-height: 24px;
		letter-spacing: 0.05em;
		color: #323232;
		margin-top: 0;
		margin-bottom: 60px;
	}
	&__img {
		max-width: 845px;
		width: 100%;
		height: 496.5px;
		position: absolute;
		right: -63px;
		bottom: 0;
	}
	.scroll__btn {
		left: 170px;
		bottom: 250px;
	}
	&__background {
		position: absolute;

		background-repeat: no-repeat;
		background-position: center center;
		background-size: 100%;
		display: block;

		z-index: -1;
		&_1 {
			width: 965px;
			height: 785px;
			top: 0;
			right: 0;
		}
		&_2 {
			height: 500px;
			width: 100%;
			top: 63px;
			left: 0;
		}
	}
	&__line {
		background-repeat: no-repeat;
		background-position: center center;
		background-size: 100%;
		position: absolute;
		z-index: -1;
		&_1 {
			background-image: url(../img/hero-line_1.svg);
			height: 180px;
			width: 295px;
			top: 0;
			right: 530px;
		}
		&_2 {
			background-image: url(../img/hero-line_2.svg);
			top: 409px;
			right: 0;
			width: 377px;
			height: 250px;
		}
		// border: 1.8px solid rgba(36, 176, 213, 0.4);
		// box-shadow: 0px 0px 4px #65b1db;
		// filter: blur(1px);
	}
	@include _mobile-tablet {
		min-height: auto;
		padding-top: 130px;
		min-height: 750px;
		top: -80px;
		&__container {
			flex-direction: column;
		}
		&__text {
			display: flex;
			align-items: center;
			justify-content: center;
			align-content: center;
			flex-direction: column;
			text-align: center;
			margin-bottom: 25px;
		}
		&__title {
			font-size: 28px;
			line-height: 60px;
			color: #fff;
			margin-bottom: 15px;
			span:first-child {
				font-size: 28px;
				line-height: 37px;
			}
		}

		.scroll__btn {
			display: none;
		}

		&__subtitle {
			font-size: 14px;
			line-height: 18px;
			color: #ffffff;
			margin-bottom: 30px;
		}

		&__img {
			position: relative;
			height: 240px;
			width: 100%;
			left: -70px;
			right: 0;
			img {
				width: 100%;
				height: 100%;
			}
		}
		&__line {
			display: none;
		}
		&__background {
			&_1 {
				// width: 300px;
			}
		}
	}
	@media (min-width: 1200px) and (max-width: 1644px) {
		&__img {
			right: 0;
		}
		&__background {
			&_1 {
				width: 615px;
				height: 500px;
			}
		}
		&__line {
			&_1 {
				right: 300px;
			}
			&_2 {
				top: 100px;
			}
		}
		.scroll__btn {
			display: none;
		}
	}
	@media (min-width: 1645px) and (max-width: 1820px) {
		&__img {
			right: 0;
		}
		&__background {
			&_1 {
				width: 870px;
				height: 710px;
			}
		}
	}
}
