.whom {
	margin-bottom: 130px;
	&__title {
		@extend %section-title;
		margin-bottom: 30px;
		text-align: center;
		span {
			display: block;
		}
	}
	&__subtitle {
		@extend %subtitle_1;
		max-width: 630px;
		margin-right: auto;
		margin-left: auto;
		margin-top: 0;
		margin-bottom: 40px;
	}
	&__row {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		align-content: space-between;
		max-width: 735px;
		margin-right: auto;
		margin-left: auto;
	}
	&__item {
		max-width: 290px;
	}
	@include _mobile {
		margin-bottom: 100px;
		&__title {
			margin-bottom: 20px;
		}
		&__subtitle {
			margin-bottom: 50px;
		}
		&__item {
			margin-right: 20px;
			&:last-child {
				margin-right: 0;
			}
		}
	}
	@include _tablet {
		margin-bottom: 100px;
	}
}
.whom-item {
	display: flex;
	align-items: center;
	justify-content: center;
	align-content: center;
	flex-direction: column;
	text-align: center;
	position: relative;
	z-index: 1;
	&:after {
		content: '&';
		position: absolute;
		font-weight: 900;
		font-size: 80px;
		line-height: 1;
		text-align: center;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: $accent-color;
		text-shadow: 0px 0px 10px rgba(64, 182, 203, 0.5);
		right: -105px;
		top: 50%;
		transform: translateY(-50%);
	}
	&:last-child::after {
		content: none;
	}
	&__img {
		max-height: 250px;
		margin-bottom: 35px;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	&__img-bg {
		position: absolute;
		background-repeat: no-repeat;
		background-position: center center;
		background-size: 100% 100%;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		width: 240px;
		height: 290px;
		z-index: -1;
	}
	p {
		@extend %p_4;
		margin-top: 0;
		margin-bottom: 0;
	}
	@include _mobile {
		&::after {
			font-size: 30px;
			line-height: 24px;
			top: 65px;
			right: 0%;
			transform: translateX(100%);
		}
		&__img {
			width: 88px;
			height: 135px;
			margin-bottom: 10px;
			img {
				width: auto;
				height: 100%;
			}
		}
		&__img-bg {
			width: 105px;
			height: 151px;
			img {
				width: 100%;
				height: auto;
			}
		}
	}
}
