.page {
	position: relative;
	z-index: 1;

	&__decoration {
		position: absolute;
		z-index: -1;
		background-repeat: no-repeat;
		background-position: center center;
		background-size: 100%;
		display: block;
		&_1 {
			height: 1010px;
			left: 0;
			top: 475px;
			width: 300px;
		}
		&_2 {
			width: 100%;
			height: 1280px;
			top: 926px;
			left: 0;
		}
		&_3 {
			transform: scale(1, 1) rotate(180deg);
			height: 1010px;
			width: 300px;
			right: 0;
			top: 1395px;
		}
		&_4 {
			top: 2740px;
			width: 1050px;
			// width: 100%;
			left: 0;
			height: 856px;
		}
		&_5 {
			top: 3320px;
			width: 100%;
			left: 0;
			height: 260px;
		}
		&_6 {
			height: 1430px;
			width: 500px;
			top: 3530px;
			right: 0;
		}
		&_7 {
			height: 1650px;
			width: 160px;
			top: 4315px;
			left: 0;
		}
		&_8 {
			top: 6470px;
			height: 1106px;
			left: 0;
			width: 100%;
		}
		&_9 {
			top: 7145px;
			width: 520px;
			height: 1870px;
			left: 0%;
		}
		&_10 {
			transform: scale(1, 1) rotate(180deg);
			height: 1650px;
			width: 200px;
			top: 8905px;
			right: 0;
		}
		&_11 {
			top: 8755px;
			width: 100%;
			left: 0;
			height: 1224px;
		}
		&_12 {
			width: 100%;
			height: 457px;
			top: 1075px;
			right: 0;
		}
		&_13 {
			height: 800px;
			width: 100%;
			left: 0;
			top: 2995px;
		}
		&_14 {
			width: 100%;
			height: 457px;
			top: 7425px;
			right: 0;
		}
		&_15 {
			width: 100%;
			height: 457px;
			top: 10905px;
			right: 0;
		}
	}
	@include _mobile {
		overflow-x: hidden;
		&__decoration {
			display: none;
		}
	}
	@include _tablet {
		&__decoration {
			&_1 {
				display: none;
			}
			&_4 {
				display: none;
			}
			&_8 {
				display: none;
			}
		}
	}
}
