@mixin _mobile {
	@media (max-width: 767px) {
		@content;
	}
}
@mixin _mobile-tablet {
	@media (max-width: 1199px) {
		@content;
	}
}
@mixin _tablet {
	@media (min-width: 768px) and (max-width: 1199px) {
		@content;
	}
}

@mixin _pc {
	@media (min-width: 1200px) {
		@content;
	}
}

@mixin _mid-pc {
	@media (min-width: 1200px) and (max-width: 1579px) {
		@content;
	}
}
