.best-price {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	align-content: flex-start;
	flex-direction: column;
	margin-bottom: 135px;
	&__title {
		@extend %title_2;
		margin-bottom: 50px;
		text-align: center;
		span {
			display: block;
			text-transform: uppercase;
			background: linear-gradient(
				0deg,
				rgba(221, 26, 143, 1) 0%,
				rgba(254, 17, 126, 1) 100%
			);
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
		}
	}
	&__tabel {
		max-width: 1040px;
		width: 100%;
		margin-bottom: 60px;
	}
	@include _mobile {
		margin-bottom: 100px;
		&__title {
			margin-bottom: 50px;
		}
		&__tabel {
			margin-bottom: 50px;
		}
	}
	@include _tablet {
		margin-bottom: 100px;
	}
}

.best-price-tabel {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	align-content: flex-start;
	flex-direction: column;
	position: relative;
	&__heading {
		background-color: $accent-color;
		padding: 15px 30px;
		border-radius: 15px;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		align-content: flex-start;
		@extend %p_6;
		color: #fff;
		p {
			width: 56%;
			margin-top: 0;
			margin-bottom: 0;
		}
		p:last-child {
			width: 44%;
		}
	}
	&__row {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		align-content: flex-start;
		padding: 15px 30px;
		position: relative;
		@extend %p_6;
		font-weight: 600;
		&::after {
			content: '';
			width: 100%;
			height: 1px;
			background-color: $accent-color;
			position: absolute;
			left: 0;
			border-radius: 30px;
			bottom: 0;
			box-shadow: 0px 0px 10px rgba(72, 164, 213, 0.1);
		}
		&:nth-last-child(-n + 2) {
			padding-bottom: 0;
			&:after {
				content: none;
			}
		}
		p {
			width: 56%;
			margin-top: 0;
			margin-bottom: 0;
		}
		p:last-child {
			width: 44%;
		}
	}
	&__img {
		position: absolute;
		right: -195px;
		width: 272px;
		height: 360px;
		top: -23px;
	}
	@include _mobile {
		&__heading {
			padding: 7px 10px;
			border-radius: 4.31752px;
			justify-content: space-between;
			align-content: space-between;
			p:first-child {
				width: 39%;
			}
			p:last-child {
				width: 45%;
			}
		}
		&__row {
			padding: 10px;
		}
		&__img {
			display: none;
		}
	}
	@include _tablet {
		&__img {
			display: none;
		}
	}
}
