.discount {
	display: flex;
	align-items: center;
	justify-content: center;
	align-content: center;
	flex-direction: column;
	margin-bottom: 115px;
	&__title {
		@extend %section-title;
		margin-bottom: 45px;
		text-align: center;
		span {
			display: block;
		}
	}
	&__row {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		align-content: space-between;
	}
	&__img {
		max-width: 283px;
		height: 300px;
	}
	&__text {
		max-width: 680px;
		width: 100%;
		position: relative;
		&::after {
			content: '';
			width: 32px;
			height: 95px;
			background-image: url(../img/notice.svg);
			background-position: center center;
			background-size: 100% 100%;
			background-repeat: no-repeat;
			display: block;
			position: absolute;
			left: -77px;
			top: 0;
		}
		p {
			@extend %p_5;
			margin-top: 0;
			margin-bottom: 40px;
			span {
				font-weight: 900;
				background: linear-gradient(
					0deg,
					rgba(221, 26, 143, 1) 0%,
					rgba(254, 17, 126, 1) 100%
				);
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent;
			}
		}
		.btn__primary {
			color: $text-color;
		}
	}
	@include _mobile {
		margin-bottom: 60px;
		&__title {
			margin-bottom: 50px;
		}
		&__row {
			flex-direction: column;
			justify-content: center;
			align-items: center;
			text-align: center;
		}
		&__img {
			max-width: 165px;
			height: 176px;
		}
		&__text {
			display: flex;
			align-items: center;
			justify-content: center;
			align-content: center;
			flex-direction: column;
			&::after {
				left: 0;
			}
			p {
				text-align: left;
				width: 80%;
				margin-left: auto;
				br {
					display: none;
				}
			}
		}
		.btn__primary {
			border-image-outset: 0;
			border-image-slice: 23;
		}
	}
	@include _tablet {
		margin-bottom: 100px;
		&__row {
			flex-direction: column;
		}
		&__img {
			position: static;
		}
		&__text {
			display: flex;
			align-items: center;
			justify-content: center;
			align-content: center;
			flex-direction: column;
			text-align: center;
			&::after {
				left: -20px;
			}
		}
	}
}
