  min-width: 210px;
  border-radius: 30px;
  width: fit-content;
}

.btn__size_2 {
  padding: 20px 10px;
  border-radius: 15px;
  min-width: 240px;
  width: fit-content;
}

.btn__size_3 {
  width: fit-content;
  min-width: 340px;
  padding: 17px 10px;
  border-radius: 15px;
}

.btn__size_4 {
  width: fit-content;
  min-width: 290px;
  padding: 15px 10px;
  border-radius: 30px;
}

.btn__size_5 {
  width: fit-content;
  min-width: 370px;
  padding: 17px 20px;
  border-radius: 15px;
}

.btn__size_6 {
  width: fit-content;
  min-width: 330px;
  padding: 13px 20px;
  border-radius: 15px;
}

.btn__size_7 {
  width: fit-content;
  min-width: 330px;
  padding: 16px 20px;
  border-radius: 15px;
}

.btn__size_8 {
  width: fit-content;
  min-width: 370px;
  padding: 17px 20px;
  border-radius: 15px;
