.btn__icon {
  padding-left: 65px;
}

.btn__icon::after {
  content: '';
  width: 45px;
  height: 75px;
  background-repeat: no-repeat;
  background-image: url(../img/btn-icon.svg);
  background-position: center center;
  background-size: 100% 100%;
  left: 20px;
  bottom: 7px;
  position: absolute;
}

.btn__icon_2::after {
  left: 8px;
