.treatment-include {
	display: flex;
	align-items: center;
	justify-content: center;
	align-content: center;
	flex-direction: column;
	margin-bottom: 130px;
	&__title {
		@extend %section-title;
		text-align: center;
		margin-bottom: 60px;
		span {
			display: block;
		}
	}
	&__row {
		display: flex;
		align-items: flex-start;
		justify-content: center;
		align-content: center;
		flex-wrap: wrap;
	}
	&__item {
		max-width: 260px;
		width: 100%;
		margin-right: 30px;
		margin-bottom: 60px;
	}
	@include _mobile {
		margin-bottom: 100px;
		&__row {
			margin-bottom: 70px;
		}
		&__title {
			margin-bottom: 50px;
		}
		&__item {
			max-width: none;
			margin-right: 20px;
			margin-bottom: 30px;
			width: 46.6%;
			&:nth-child(2n-2),
			&:last-child {
				margin-right: 0;
			}
			&:last-child {
				margin-bottom: 0;
			}
		}
		.btn__primary {
		}
	}
	@include _tablet {
		margin-bottom: 100px;
		&__row {
			margin-bottom: 50px;
		}
		&__item {
			&:nth-child(2n -2),
			&:last-child {
				margin-right: 0;
			}
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	@include _pc {
		&__item {
			&:nth-child(4n-4),
			&:last-child {
				margin-right: 0;
			}
		}
	}
}
.treatment-include-item {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	align-content: center;
	flex-direction: column;
	text-align: center;
	&__icon {
		width: 66px;
		height: 66px;
		border-radius: 50%;
		background: linear-gradient(180deg, #42e8e0 9.66%, #393091 100%);
		box-shadow: 0px 0px 10px rgba(69, 255, 255, 0.5);
		display: flex;
		align-items: center;
		justify-content: center;
		align-content: center;
		margin-bottom: 20px;
		svg {
			width: 38px;
			height: 38px;
			filter: drop-shadow(0px 0px 10px rgba(255, 255, 255, 0.5));
		}
	}
	p {
		@extend %p_14;
		margin-top: 0;
		margin-bottom: 0;
	}
	@include _mobile {
		&__icon {
			width: 50px;
			height: 50px;
			svg {
				width: 28px;
				height: 28px;
			}
		}
	}
}
