.what-is {
	display: flex;
	align-items: center;
	justify-content: center;
	align-content: center;
	flex-direction: column;
	position: relative;
	&__title {
		@extend %section-title;
		margin-top: 0;
		margin-bottom: 30px;
		text-align: center;
		span {
			display: block;
		}
	}
	&__subtitle {
		max-width: 970px;
		font-weight: normal;
		font-size: 18px;
		line-height: 24px;
		text-align: center;
		letter-spacing: 0.05em;
		color: $text-color;
		margin-top: 0;
		margin-bottom: 70px;
	}
	&__feature {
		width: 100%;
		margin-bottom: 75px;
	}
	&__decoration {
		display: none;
	}
	@include _mobile {
		&__title {
			margin-bottom: 25px;
			span {
				font-size: 23px;
			}
		}
		&__subtitle {
			font-size: 13px;
			line-height: 18px;
			margin-bottom: 55px;
		}
		&__feature {
			margin-bottom: 70px;
		}
		&__decoration {
			display: block;
			position: absolute;
			z-index: -1;
			height: 350px;
			width: 620px;
			bottom: 40px;
			left: -300px;
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 100% 100%;
		}
	}
	@include _tablet {
		&__decoration {
			display: block;
			position: absolute;
			z-index: -1;
			height: 350px;
			width: 620px;
			bottom: 30%;
			left: -300px;
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 100% 100%;
		}
	}
}
.what-is-feature {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	align-content: flex-start;

	&__img {
		margin-right: 70px;
		width: 335px;
		height: 500px;
		mix-blend-mode: normal;
		filter: drop-shadow(0px 4px 21px rgba(57, 48, 145, 0.51));
	}
	&__text {
		max-width: 475px;
		position: relative;
		width: 100%;
		padding: 30px 25px;
		border: 2px solid #b8dbee;
		background-color: #fff;
		box-shadow: 0px 4px 10px rgba(15, 5, 76, 0.15);
		backdrop-filter: blur(14px);
		border-radius: 40px;
		&::after {
			content: '';
			background-image: url(../img/triangle.svg);
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 100%;
			width: 34px;
			height: 71px;
			position: absolute;
			left: -34px;
			top: 38px;
		}
		p {
			font-weight: normal;
			font-size: 17px;
			line-height: 22px;
			text-align: center;
			letter-spacing: 0.05em;
			color: $text-color;
			margin-top: 0;
			margin-bottom: 30px;
			span {
				font-weight: 900;
				font-size: 20px;
				line-height: 22px;
				text-transform: uppercase;
				background: linear-gradient(
					0deg,
					rgba(221, 26, 143, 1) 0%,
					rgba(254, 17, 126, 1) 100%
				);
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent;
			}
			&:last-child {
				font-weight: bold;
				color: #264795;
				text-transform: uppercase;
				margin-bottom: 0;
			}
		}
	}
	@include _mobile {
		&__img {
			margin-right: 0;
			width: 160px;
			height: 204px;
			img {
				height: 100%;
				width: auto;
			}
		}
		&__text {
			padding: 13px 10px;
			width: 77%;
			&::after {
				width: 14px;
				height: 30px;
				left: -14px;
			}
			p {
				font-weight: 900;
				font-size: 8.35308px;
				line-height: 9px;
				margin-bottom: 14px;
				span {
					font-weight: 900;
					font-size: 8.35308px;
					line-height: 9px;
				}
				&:last-child {
					font-weight: bold;
					font-size: 7.10012px;
					line-height: 9px;
				}
			}
		}
	}
}
