.header {
	padding: 22px 0;
	background-color: transparent;
	position: relative;
	z-index: 10;
	&_white {
		background-color: #fff;
	}
	&__container {
		display: flex;
		align-items: center;
		justify-content: space-between;
		align-content: space-between;
	}
	&__nav {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		align-content: flex-start;
	}
	&__logo {
		width: 160px;
		height: 60px;
		margin-right: 10px;
		position: relative;
		left: -20px;
		svg {
			width: 100%;
			height: 100%;
		}
	}
	&__menu {
		margin: 0;
		padding: 0;
	}
	&__registration_white {
		background-color: #fff;
		color: $text-color !important;
	}
	&__mobile-btn,
	&__mobile-btn-close {
		display: none;
	}

	@include _mobile-tablet {
		padding: 20px 0;
		&__nav {
			position: fixed;
			height: 100vh;
			display: block;
			transition: 0.3s all;
			transform: translateX(100%);
			right: 0;
			top: 0;
			max-width: 270px;
			width: 100%;
			background: rgba(15, 5, 76, 0.6);
			backdrop-filter: blur(9px);
		}
		&__logo {
			left: 0;
			width: 105px;
			height: 40px;
		}
		&__registration {
			margin-right: 10px;
			margin-left: auto;
		}
		&__menu {
			margin-bottom: 35px;
		}
		&__mobile-btn {
			display: flex;
			height: 17px;
			width: 24px;
			align-items: flex-start;
			justify-content: space-between;
			align-content: space-between;
			flex-direction: column;
			cursor: pointer;
			span {
				width: 24px;
				height: 3px;
				background-color: #fff;
				border-radius: 2px;
				box-shadow: 0px 0px 10px rgba(255, 255, 255, 0.5);
			}
			&-close {
				padding: 0;
				margin: 0;
				display: flex;
				align-items: center;
				justify-content: center;
				align-content: center;
				margin-top: 25px;
				margin-right: 10px;
				margin-left: auto;
				svg {
					width: 24px;
					height: 24px;
				}
			}
		}
	}
}

.header-menu {
	list-style: none;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	align-content: flex-start;
	min-width: 440px;
	width: fit-content;
	&__item {
		font-weight: 600;
		font-size: 16px;
		line-height: 21px;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		display: flex;
		align-items: center;
		justify-content: center;
		align-content: center;
		a {
			color: $text-color;
			text-decoration: none;
			transition: 0.3s all;
			padding: 20px;

			&:hover {
				color: #40b6cb;
				text-shadow: 0px 0px 2px #40b6cb;
			}
		}
	}
	@include _mobile-tablet {
		flex-direction: column;
		min-width: auto;
		width: 100%;
		&__item {
			justify-content: flex-end;
			align-content: flex-end;
			width: 100%;
			a {
				width: 100%;
				height: 100%;
				text-align: right;
				color: #fff;
				padding: 12.5px 10px;
			}
		}
	}
}
