.stages {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	align-content: flex-start;
	flex-direction: column;
	&__title {
		@extend %section-title;
		margin-top: 0;
		margin-bottom: 30px;
	}
	&__subtitle {
		font-weight: normal;
		font-size: 20px;
		line-height: 1;
		text-align: center;
		letter-spacing: 0.05em;
		color: $text-color;
		margin-top: 0;
		margin-bottom: 60px;
	}
	&__img {
		position: absolute;
		text-align: center;
		img {
			margin-bottom: 20px;
		}
	}
	&__img-employer {
		top: 0;
		left: 0;
	}
	&__img-alita {
		right: -100px;
		top: 80px;
		&_left {
			transform: scale(1, -1) rotate(180deg);
			right: 360px;
			top: 760px;
		}
	}
	&__img-candidate {
		right: -90px;
		top: 620px;
		&_right {
			transform: scale(1, -1) rotate(180deg);
			top: 880px;
			left: -55px;
			right: auto;
		}
	}
	&__img-candidates {
		bottom: 140px;
		right: 300px;
	}
	&__img-text {
		font-weight: bold;
		font-size: 18px;
		line-height: 18px;
		text-align: center;
		letter-spacing: 0.05em;
		color: $text-color;
		&_blue {
			color: $accent-color;
		}
		&_pink {
			background: linear-gradient(
				0deg,
				rgba(221, 26, 143, 1) 0%,
				rgba(254, 17, 126, 1) 100%
			);
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
		}
	}
	@include _mobile-tablet {
		margin-bottom: 100px;
		&__row {
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;
			justify-content: space-between;
			align-content: space-between;
			position: relative;
			max-width: 660px;
		}
		&-card {
			width: 43.75%;
			margin-bottom: 45px;
			max-width: none;
			&:nth-child(even) {
				margin-right: 20px;
			}
		}
		&__img {
			position: static;
			width: 45%;
			display: flex;
			align-items: center;
			justify-content: center;
			align-content: center;
			flex-direction: column;
			img {
				margin-bottom: 10px;
				width: auto;
			}
			&-employer {
				width: 100%;
				margin-bottom: 30px;
				img {
					height: 78px;
				}
			}
			&-alita img {
				height: 77px;
			}
			&-candidate img {
				height: 69px;
			}
			&-candidates img {
				height: 100px;
			}
		}
		&__img-text {
			font-size: 12px;
			line-height: 13px;
		}
	}
	@include _pc {
		&__row {
			display: grid;
			grid-template-columns: 1fr 1fr 1fr;
			grid-template-rows: 252px 272px 272px 270px 270px;
			gap: 0px 0px;
			position: relative;
			grid-template-areas:
				'top top .'
				'second second .'
				// 'middle middle middle'
				'third third .'
				'fourth fourth .'
				'fifth fifth .';
			max-width: 942px;
			width: 100%;
			background-image: url(../img/stage-lines.svg);
			background-repeat: no-repeat;
			background-position: 0 47px;
			background-size: 100%;
		}
		.top {
			grid-area: top;
			margin-left: 170px;
		}
		.second {
			grid-area: second;
			justify-self: center;
		}
		.middle {
			position: absolute;
			top: 395px;
			left: -15px;
		}
		.third {
			grid-area: third;
			justify-self: center;
		}
		.fourth {
			grid-area: fourth;
			margin-left: 120px;
		}
		.fifth {
			grid-area: fifth;
			margin-left: 120px;
		}
		&-card {
			&_left {
				flex-direction: row;
				align-items: center;
				justify-content: flex-start;
				align-content: flex-start;
				text-align: left;
				.stages-card__icon {
					margin-bottom: 0;
					margin-top: 0;
					margin-right: 15px;
				}
				.stages-card__number {
					text-align: left;
				}
				.stages-card__text {
					text-align: left;
				}
			}
			&__text-wrapper {
				max-width: 145px;
			}
			&_right {
				margin-left: 50px;
			}
			&_right-2 {
				margin-left: 90px;
			}
			&_right-3 {
				margin-left: 80px;
			}
		}
	}
}
