.btn {
	&__primary {
		font-family: $primary-font;
		font-weight: 600;
		font-size: 18px;
		line-height: 1;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: $text-color;
		display: flex;
		align-items: center;
		justify-content: center;
		align-content: center;
		text-decoration: none;
		position: relative;
		@include _mobile {
			font-size: 12px;
			line-height: 16px;
		}
	}
	&__small-font {
		font-size: 16px;
		@include _mobile {
			font-size: 12px;
			line-height: 16px;
		}
	}
	&__blue-border {
		background: transparent;
		background-repeat: no-repeat;
		// border: 3px;
		// border-style: solid;
		border: 3px solid $accent-color;
		// border-image-repeat: stretch stretch;
		// border-image-slice: 30;
		// border-image-width: 210px;
		// border-image-outset: 6px;
		// border-radius: 30px;
		// border-image-source: url(../img/border.svg);
		transition: 0.3s all;
		filter: drop-shadow(0px 0px 6px #40b6cb);
		color: #fff;
		&:hover {
			box-shadow: 0px 0px 20px #40b6cb;
		}
		&_radius {
			border-radius: 15px;
			border-image-source: url(../img/border-with-radius.svg);
		}
	}
	&__blue {
		background-color: $dark-blue;
		color: #fff;
		box-shadow: 0px 0px 5px #393091;
		transition: 0.3s all;
		&:hover {
			background-color: $accent-color;
			box-shadow: 0px 0px 15px $accent-color;
		}
	}
	&__size {
		&_1 {
			padding: 15px 10px 15px;
			min-width: 210px;
			border-radius: 30px;
			width: fit-content;
		}
		&_2 {
			padding: 20px 10px;
			border-radius: 15px;
			min-width: 240px;
			width: fit-content;
		}
		&_3 {
			width: fit-content;
			min-width: 340px;
			padding: 17px 10px;
			border-radius: 15px;
		}
		&_4 {
			width: fit-content;
			min-width: 290px;
			padding: 15px 10px;
			border-radius: 30px;
		}
		&_5 {
			width: fit-content;
			min-width: 370px;
			padding: 17px 20px;
			border-radius: 15px;
		}
		&_6 {
			width: fit-content;
			min-width: 330px;
			padding: 13px 20px;
			border-radius: 15px;
		}
		&_7 {
			width: fit-content;
			min-width: 330px;
			padding: 16px 20px;
			border-radius: 15px;
		}
		&_8 {
			width: fit-content;
			min-width: 370px;
			padding: 17px 20px;
			border-radius: 15px;
		}
	}
	&__icon {
		padding-left: 65px;
		&::after {
			content: '';
			width: 45px;
			height: 75px;
			background-repeat: no-repeat;
			background-image: url(../img/btn-icon.svg);
			background-position: center center;
			background-size: 100% 100%;
			left: 20px;
			bottom: 7px;
			position: absolute;
		}
		&_2 {
			&::after {
				left: 8px;
			}
		}
		&_3 {
			&::after {
				left: 5px;
			}
		}
	}
	@include _mobile {
		&__size {
			&_2 {
				padding: 16px 10px;
			}
			&_3 {
				min-width: 245px;
				padding: 16px 10px;
			}
			&_4 {
				min-width: 240px;
			}
			&_5 {
				min-width: 228px;
				padding: 14px 5px;
				padding-left: 35px !important;
			}
			&_6 {
				min-width: 240px;
				padding: 15px 10px;
			}
			&_7 {
				min-width: 240px;
			}
			&_8 {
				min-width: 300px;
			}
		}
		&__icon {
			padding-left: 55px;
			&::after {
				left: 8px;
			}
			&_2 {
				&::after {
					width: 22px;
					height: 36px;
				}
			}
		}
	}
}
.scroll__btn {
	width: 24px;
	height: 24px;
	position: absolute;
	margin: 0;
	padding: 0;
	svg {
		width: 100%;
		height: 100%;
	}
	&:before {
		content: '';
		position: absolute;
		width: 4px;
		height: 150px;
		background: linear-gradient(180deg, #42e8e0 9.66%, #393091 100%);
		border-radius: 5px;
		margin-bottom: 10px;
		bottom: 34px;
		left: 50%;
		transform: translateX(-50%);
	}
}
