.footer {
	padding: 30px 0;
	position: relative;
	z-index: 2;
	&__row {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		align-content: space-between;
		padding-bottom: 40px;
		border-bottom: 1px solid #e5e5e5;
		margin-bottom: 30px;
	}
	&__column {
		width: 13.5%;
		margin-right: 70px;
		&:last-child {
			margin-right: 0;
		}
		&_medium {
			width: 15.5%;
		}
		&_big {
			width: 23%;
			margin-right: 30px;
		}
	}
	&__logo {
		width: 183px;
		height: 70px;
		margin-bottom: 20px;
		svg {
			width: 100%;
			height: 100%;
		}
	}
	&__copyright {
		p {
			@extend %p_11;
			margin-top: 0;
			margin-bottom: 0;
			color: #6d6d6d;
		}
	}

	@include _mobile-tablet {
		padding-top: 0;
		padding-bottom: 15px;
		&__row {
			flex-wrap: wrap;
			padding-bottom: 0;
			margin-bottom: 50px;
			border-bottom: none;
		}
		&__column {
			width: 43%;
			margin-right: 0;
			align-items: center;
			justify-content: center;
			align-content: center;
			display: flex;
			flex-direction: column;
			text-align: center;
			margin-bottom: 50px;
			&:nth-last-child(-n + 2) {
				margin-bottom: 0;
			}
			&:nth-child(even) {
				margin-right: 40px;
			}
			&_big {
				width: 100%;
				p {
					width: 86.67%;
				}
			}
		}
	}
}

.footer-column {
	&__title {
		@extend %subtitle_3;
		color: $accent-color;
		margin-bottom: 10px;
	}
	p {
		@extend %p_8;
		margin-top: 0;
		margin-bottom: 0;
	}
	ul {
		padding: 0;
		margin: 0;
		list-style: none;
		li {
			width: 100%;
			a {
				@extend %p_9;
				text-decoration: none;
				transition: 0.3s all;
				&:hover {
					color: $accent-color;
				}
			}
		}
	}
}
.lang {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	align-content: flex-start;
	flex-direction: row;

	&__item {
		width: auto !important;
		a {
			@extend %p_10;
			display: flex;
			width: fit-content;
			color: #6d6d6d !important;
		}
		&_active {
			a {
				color: $accent-color !important;
				&:after {
					content: '/';
					color: $accent-color;
					margin-right: 10px;
					margin-left: 10px;
				}
			}
		}
		&:last-child {
			a {
				flex-direction: row-reverse;
			}
		}
	}
	@include _mobile-tablet {
		margin-top: 15px !important;
		&__item {
			a {
				font-size: 18px !important;
				line-height: 20px !important;
			}
		}
	}
}
