.price {
	display: flex;
	align-items: center;
	justify-content: center;
	align-content: center;
	flex-direction: column;
	margin-bottom: 40px;
	&__title {
		@extend %section-title;
		margin-bottom: 30px;
		text-align: center;
		span {
			display: block;
		}
	}
	&__subtitle {
		@extend %subtitle_2;
		max-width: 980px;
		text-align: center;
		margin-top: 0;
		margin-bottom: 65px;
		span {
			color: #fe117e;
		}
	}
	@include _mobile {
		margin-bottom: 50px;
		&__title {
			margin-bottom: 40px;
			span {
				word-break: break-all;
			}
		}
		&__subtitle {
			margin-bottom: 100px;
		}
	}
}

.price-per-one {
	max-width: 900px;
	width: 100%;
	padding: 25px 45px 25px 190px;
	border-radius: 15px;
	background: rgba(88, 255, 254, 0.1);
	display: flex;
	align-items: center;
	justify-content: flex-start;
	align-content: flex-start;
	position: relative;
	&__icon {
		width: 110px;
		height: 110px;
		position: absolute;
		left: 45px;
		bottom: 18px;
	}
	&__description {
		@extend %p_13;
		margin-right: 75px;
		margin-top: 0;
		margin-bottom: 0;
		br {
			display: none;
		}
	}
	&__price {
		display: flex;
		align-items: flex-end;
		justify-content: flex-start;
		align-content: flex-start;
		font-weight: 900;
		font-size: 48px;
		line-height: 40px;
		letter-spacing: 0.05em;
		margin-top: 0;
		margin-bottom: 0;
		// background: linear-gradient(180deg, #ff6dba 0%, #fe117e 100%);
		// -webkit-background-clip: text;
		// -webkit-text-fill-color: transparent;
		color: #fe117e;
		span {
			font-size: 30px;
			line-height: 1;
		}
	}
	@include _mobile-tablet {
		flex-direction: column;
		text-align: center;
		padding: 30px 16px;
		justify-content: center;
		align-content: center;
		&__icon {
			left: 0;
			top: -75px;
		}
		&__description {
			margin-right: 0;
			margin-bottom: 30px;
		}
		&__price {
			font-size: 40px;
			line-height: 40px;
			span {
				font-size: 18px;
				line-height: 1;
			}
		}
	}
	// @include _tablet {
	// 	padding: 20px;
	// 	padding-left: 40px;
	// 	flex-direction: column;
	// 	&__description {
	// 		margin-right: 0;
	// 	}
	// }
}
