.faq {
	display: flex;
	align-items: center;
	justify-content: center;
	align-content: center;
	flex-direction: column;
	position: relative;
	margin-bottom: 130px;
	&__title {
		@extend %section-title;
		margin-bottom: 70px;
		text-align: center;
		span {
			display: block;
		}
	}
	&__column {
		width: 100%;
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		align-content: flex-start;
		flex-direction: column;
	}
	&__item {
		margin-bottom: 20px;
	}
	&__img {
		position: absolute;
		top: 120px;
		left: -262px;
		width: 250px;
		height: 345px;
	}

	@include _mobile {
		margin-bottom: 100px;
		&__title {
			margin-bottom: 60px;
		}
		&__img {
			display: none;
		}
	}
	@include _tablet {
		margin-bottom: 100px;
		&__img {
			display: none;
		}
	}
}

.faq-item {
	width: 100%;
	&__question {
		width: 100%;
		padding: 15px 30px;
		background-color: $accent-color;
		box-shadow: 0px 0px 10px rgba(72, 164, 213, 0.5);
		border-radius: 15px;
		cursor: pointer;
		color: #fff;
		position: relative;
		transition: 0.3s all;
		@extend %p_6;
		p {
			margin-top: 0;
			margin-bottom: 0;
		}
		&::after {
			content: '';
			position: absolute;
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 100% 100%;
			width: 24px;
			height: 24px;
			right: 30px;
			top: 17px;
			background-image: url(../img/plus.svg);
			transition: 0.3s all;
		}
		&_active {
			margin-bottom: 30px;

			&::after {
				background-image: url(../img/minus.svg);
			}
		}
	}

	&__answer {
		padding: 0 90px 0 30px;
		display: none;
		@extend %p_7;
		p {
			margin-top: 0;
			margin-bottom: 0;
		}
	}
	@include _mobile {
		&__question {
			padding: 20px 10px;
			font-size: 16px;
			line-height: 28px;
			p {
				width: 85%;
			}
			&::after {
				top: 50%;
				right: 10px;
				transform: translateY(-50%);
			}
		}
		&__answer {
			padding: 0;
		}
	}
}
