.free-consultation {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	align-content: flex-start;
	flex-direction: column;
	margin-bottom: 140px;
	&__title {
		@extend %section-title;
		text-align: center;
		margin-bottom: 30px;
		margin-top: 0;
		max-width: 650px;
	}
	&__subtitle {
		font-size: 24px;
		line-height: 32px;
		text-align: center;
		letter-spacing: 0.05em;
		color: $text-color;
		margin-top: 0;
		margin-bottom: 55px;
	}
	&__list {
		list-style: none;
		padding: 0;
		margin: 0;
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		align-content: flex-start;
		flex-wrap: wrap;
		margin-bottom: 70px;
		li {
			max-width: 450px;
			width: 100%;
			margin-right: 200px;
			font-weight: 600;
			font-size: 22px;
			line-height: 29px;
			letter-spacing: 0.05em;
			color: $text-color;
			margin-bottom: 25px;
			position: relative;
			padding-left: 56px;
			&:nth-child(even) {
				margin-right: 0;
			}
			&::after {
				content: '';
				background-image: url(../img/check.svg);
				background-repeat: no-repeat;
				background-position: center center;
				background-size: 36px 36px;
				width: 36px;
				height: 36px;
				position: absolute;
				top: 0;
				left: 0;
			}
		}
	}
	&__slogan {
		font-weight: normal;
		font-size: 26px;
		line-height: 35px;
		text-align: center;
		letter-spacing: 0.01em;
		text-transform: uppercase;
		color: $text-color;
		margin-bottom: 45px;
		margin-top: 0;
		span {
			font-weight: bold;
			color: #a242ea;
			display: block;
		}
	}

	@include _mobile {
		&__title {
			margin-bottom: 20px;
		}
		&__subtitle {
			font-size: 16px;
			line-height: 21px;
			margin-bottom: 50px;
		}
		&__list {
			li {
				display: flex;
				align-items: center;
				justify-content: flex-start;
				align-content: flex-start;
				max-width: none;
				flex-direction: column-reverse;
				margin-right: 0;
				padding: 0;
				text-align: center;
				margin-bottom: 30px;
				font-size: 18px;
				line-height: 24px;
				&::after {
					position: static;
					margin-bottom: 10px;
				}
			}
		}
		&__slogan {
			font-size: 20px;
			line-height: 26px;
		}
	}
	@include _tablet {
		&__list {
			justify-content: center;
			li {
				margin-right: 0;
			}
		}
	}
}
